$colorBorder: rgba(253, 203, 168, 1);
$colorLogo: rgba(232, 232, 242, 1);
$heightBorder2: 27px;
$widthLogo: 64px;
$heightLogo: $widthLogo;
$buttonFamily: "Odibee Sans", cursive;
$buttonSize: 1.6em;
$authPadding: 15px 15px 15px 15px;
$copyrightColor: rgba(222, 222, 222, 1);
@mixin border {
  font-family: $buttonFamily;
  font-size: $buttonSize;
  border: solid 4px $colorBorder;
  padding: 2px 10px 2px 10px;
}
@mixin orders {
  font-family: "Odibee Sans", cursive;
  font-size: 1.8em;
  border: solid 4px rgba(253, 203, 168, 1);
  padding: 0px 20px 0px 20px;
  color: $colorLogo;
}

@import "body.scss";

main {
  flex: {
    direction: column;
    grow: 1;
  }
  margin-left: 10.5%;
}

.logo-left {
  display: inherit;
}

.border-logo2 {
  margin-top: 0.3px;
  max-height: $heightBorder2;
}

.logo {
  img {
    width: $widthLogo;
    position: absolute;
  }
    position: absolute;
    max-height: $heightLogo;
    margin: {
      top: 35px;
      left: 35px;
    }
}

.animated-logo {
  img {
    width: $widthLogo;
    display: none;
  }
}

.logo:hover .animated-logo img {
  display: block;
}

.logo2 {
  margin: {
    top: 50px;
    left: -80px;
  }
  font-family: "Odibee Sans", cursive;
  font-size: 2em;
}

.sign-help {
  & {
  margin: {
    left: auto;
    top: 40px;
    }
  display: flex;
  flex-direction: row;
  }
  a {
    @include border;
    justify-content: center;
  }
}

.sign-link {
  display: flex;
  justify-content: center;
  max-height: 40px;
}

#sign-auth {
  padding: $authPadding;
  opacity: 0;
  visibility: hidden;
  transition: {
    property: opacity, visibility;
    duration: 0.3s;
    delay: 0.4s;
  }
  border: solid 4px $colorBorder;
  margin-top: 10px;
}

#submit {
  background: none;
  border: 0;
  cursor: pointer;
  text-align: center;
}

.submit {
  border: solid 2px $colorBorder;
  margin: 8px 8px 5px 8px;
  display: flex;
  justify-content: center;
}

.sign-in:hover #sign-auth {
  opacity: 1;
  visibility: visible;
}

.help {
  display: flex;
  max-height: 40px;
  position: relative;
  right: 30px;
}

.order-together {
  & {
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  margin-bottom: 70px;
}
  a {
    @include orders;
  }
}

.orders {
  margin-bottom: 5%;
}

footer {
   a {
  text-decoration: none;
  color: #000;
  &:hover {
   color: #0000ff;
    }
  }
}

.copyright {
  display: flex;
  position: absolute;
  background: $copyrightColor;
  left: 0;
  width: 100%;
  font: {
    family: 'PT Sans', sans-serif;
    size: 10px;
  }
  padding: {
    top: 13px;
    bottom: 12px;
  }
}

.rules {
  margin-left: 70%;
}

.copy {
  margin-left: 2%;
}
